<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <table class="table tableText tableSm table-bordered border-primary m-0">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">
          {{ $t('shoppingOptions.product') }}
        </th>
        <th scope="col">
          {{ $t('shoppingOptions.price') }}
        </th>
        <th scope="col">{{ $t('shoppingOptions.quantity') }}</th>
        <th scope="col">{{ $t('shoppingOptions.sub') }}</th>
      </tr>
    </thead>
    <tbody class="table-group-divider luluText">
      <template v-for="(item, index) in cookieList" :key="index">
        <tr>
          <td>{{ index + 1 }}</td>
          <td>
            <div class="fw-bolder">{{ $t(item.flavor) }}</div>
            <div
              class="mb-0 d-md-flex"
              v-if="item.type === 'drink' || item.type === 'coffee'"
            >
              <p class="text-nowrap mb-0">
                {{ $t('shoppingOptions.size') }}:<span
                  class="fst-italic text-secondary mb-0"
                  >{{ item.size }}</span
                >
              </p>
              <p class="text-nowrap mb-0 ms-sm-3">
                {{ $t('shoppingOptions.drink.ice') }}:<span
                  class="fst-italic text-secondary mb-0"
                  >{{ $t(iceList[item.ice].title) }}</span
                >
              </p>
              <p class="text-nowrap ms-sm-3 mb-0">
                {{ $t('shoppingOptions.drink.sugar') }}:<span
                  class="fst-italic text-secondary"
                  >{{ $t(sugarList[item.sugar].title) }}</span
                >
              </p>
            </div>
          </td>
          <td>${{ item.price }}</td>
          <td>
            <nav aria-label="...">
              <ul class="pagination mb-0 align-items-center">
                <li class="page-item">
                  <a
                    class="page-link rounded-0"
                    href="#"
                    @click.prevent="changeQty(index, -1)"
                  >
                    -
                  </a>
                </li>
                <li class="">
                  <div class="mx-2">{{ item.quantity }}</div>
                </li>
                <li class="page-item">
                  <a
                    class="page-link rounded-0"
                    href="#"
                    @click.prevent="changeQty(index, 1)"
                  >
                    <i class="bi bi-plus-lg"></i>
                  </a>
                </li>
              </ul>
            </nav>
          </td>
          <td>
            $<span class="fw-bold text-secondary">{{
              item.price * item.quantity
            }}</span>
          </td>
        </tr>
      </template>
      <!-- 總金額  && paymethod -->
      <tr v-show="cookieList.length !== 0">
        <td colspan="5">
          <div class="card border-0">
            <h5 class="mb-3">
              {{ $t('shoppingOptions.total') }}：
              <span class="fw-bold text-secondary"
                >${{ $toCurrency(total) }}</span
              >
            </h5>
            <h5 class="mb-3">
              {{ $t('shoppingOptions.quantity') }}：
              <span class="fw-bold text-secondary">{{ listNum }}</span>
            </h5>
            <div class="d-sm-flex justify-content-start align-items-center">
              <h5>{{ $t('shoppingOptions.paymethod') }}：</h5>
              <!-- 付款方式 -->
              <div class="d-flex">
                <template v-for="(item, index) in payMethodList" :key="index">
                  <input
                    type="radio"
                    class="btn-check"
                    name="options-paymethod"
                    :id="`paymethod${index}`"
                    autocomplete="off"
                    :value="item.method"
                    v-model="payMethod"
                  />
                  <label
                    class="btn border-0 position-relative me-4"
                    :for="`paymethod${index}`"
                  >
                    {{ $t(item.method) }}
                    <CircleTwo
                      :class="{ 'd-none': payMethod !== item.method }"
                    />
                  </label>
                </template>
              </div>
            </div>
          </div>
        </td>
      </tr>
      <!-- 產生訂單 -->
      <tr v-show="cookieList.length !== 0">
        <td colspan="5" class="text-end">
          <a href="#" @click.prevent="createOrder">
            <component :is="'wired-button'" class="bg-dark text-light h4">
              <i class="bi bi-caret-right-fill text-light px-2"></i
              >{{ $t('shoppingOptions.checkout') }}
            </component>
          </a>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<style lang="scss" scoped>
.circle-2 {
  width: 6rem;
}
</style>

<script>
import Cookies from 'js-cookie';
import { apiPostOrder } from '@/assets/javascript/api';
import iceOption from '@/assets/json/iceOption.json';
import sugarOption from '@/assets/json/sugarOption.json';
import CircleTwo from '../helpers/focus/CircleTwo.vue';

export default {
  data() {
    return {
      total: 0,
      listNum: 0,
      cookieList: [],
      payMethod: 'shoppingOptions.paymethods.linePay',
      iceList: iceOption,
      sugarList: sugarOption,
      payMethodList: [
        { method: 'shoppingOptions.paymethods.cash' },
        { method: 'shoppingOptions.paymethods.linePay' },
        { method: 'shoppingOptions.paymethods.catPay' },
      ],
    };
  },
  components: {
    CircleTwo,
  },
  props: ['cookieData', 'productList'],
  watch: {
    cookieData() {
      this.cookieList = JSON.parse(JSON.stringify(this.cookieData));
      this.totalPrice();
    },
  },
  methods: {
    changeQty(index, num) {
      this.cookieList[index].quantity += num;
      if (this.cookieList[index].quantity === 0 && this.cookieList.length === 1) {
        this.cookieList.splice(index, 1);
        Cookies.remove('LULUchartList');
        this.total = 0;
      } else if (this.cookieList[index].quantity === 0) {
        this.cookieList.splice(index, 1);
        this.tempCookie = '';
        this.cookieList.forEach((item) => {
          this.tempCookie += `${JSON.stringify(item)};`;
        });
        this.tempCookie = this.tempCookie.replace(/.$/, '');
        Cookies.set('LULUchartList', this.tempCookie, {
          expires: 7,
        });
      } else {
        this.tempCookie = '';
        this.cookieList.forEach((item) => {
          this.tempCookie += `${JSON.stringify(item)};`;
        });
        this.tempCookie = this.tempCookie.replace(/.$/, '');
        Cookies.set('LULUchartList', this.tempCookie, {
          expires: 7,
        });
      }
      this.$emitter.emit('getCookie');
    },
    totalPrice() {
      this.total = 0;
      this.listNum = 0;
      this.cookieList.forEach((item) => {
        this.total += item.price * item.quantity;
        this.listNum += item.quantity;
      });
    },
    createOrder() {
      this.mealStatus = 25;
      apiPostOrder({
        productList: this.cookieList,
        payMethod: this.payMethod,
        mealStatus: this.mealStatus,
      })
        .then((res) => {
          if (Cookies.get('luluBakeOrder') === undefined) {
            Cookies.set(
              'luluBakeOrder',
              JSON.stringify({
                // eslint-disable-next-line no-underscore-dangle
                orderId: res.data.data._id,
                num: res.data.data.productList.length,
                cookieId: res.data.data.cookieId,
              }),
            );
          } else {
            let currentItem = Cookies.get('luluBakeOrder');
            currentItem = `${currentItem}; ${JSON.stringify({
              // eslint-disable-next-line no-underscore-dangle
              orderId: res.data.data._id,
              num: res.data.data.productList.length,
              cookieId: res.data.data.cookieId,
            })}`;
            Cookies.set('luluBakeOrder', currentItem);
          }
          Cookies.remove('LULUchartList');
          this.$emitter.emit('getAdminOrders');
          if (this.$route.path.split('/')[1] === 'shop') {
            this.$router.push('/order');
          } else if (this.$route.path.split('/')[1] === 'backend') {
            this.$emitter.emit('toggleModal');
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
